<script setup lang="ts">
import { ref, onMounted, nextTick } from 'vue'

export interface RoleItem {
  id: string
  label: string
  icon?: string // URL or component name
  description?: string
}

export interface Connection {
  from: string
  to: string
}

const props = defineProps<{
  leftItems: RoleItem[]
  rightItems: RoleItem[]
  connections: Connection[]
}>()

// We will use a simplified approach where we assume items are evenly spaced
// and draw lines based on index.
const getConnectorPath = (fromId: string, toId: string) => {
  const leftIndex = props.leftItems.findIndex(i => i.id === fromId)
  const rightIndex = props.rightItems.findIndex(i => i.id === toId)
  
  if (leftIndex === -1 || rightIndex === -1) return ''

  // Assuming 6rem (96px) height per item + gap
  const itemHeight = 96
  const startY = leftIndex * itemHeight + 48 // Center of item
  const endY = rightIndex * itemHeight + 48
  
  const startX = 0
  const endX = 200 // Width of SVG area

  // Cubic bezier curve
  const controlX1 = startX + 100
  const controlX2 = endX - 100

  return `M ${startX} ${startY} C ${controlX1} ${startY}, ${controlX2} ${endY}, ${endX} ${endY}`
}

const hoveredItem = ref<string | null>(null)

const isConnected = (itemId: string, otherId: string) => {
  return props.connections.some(c => 
    (c.from === itemId && c.to === otherId) || 
    (c.from === otherId && c.to === itemId)
  )
}

const isHighlighted = (id: string) => {
  if (!hoveredItem.value) return false
  if (hoveredItem.value === id) return true
  return isConnected(hoveredItem.value, id)
}
</script>

<template>
  <div class="flex items-start justify-center gap-0 w-full max-w-4xl mx-auto p-8">
    <!-- Left Column -->
    <div class="flex flex-col gap-0 w-64">
      <div 
        v-for="item in leftItems" 
        :key="item.id"
        class="h-24 flex items-center justify-end pr-4 relative group cursor-pointer transition-all duration-300"
        @mouseenter="hoveredItem = item.id"
        @mouseleave="hoveredItem = null"
      >
        <div 
          class="p-4 rounded-lg border-2 transition-all duration-300 w-full text-right bg-white"
          :class="isHighlighted(item.id) ? 'border-blue-500 shadow-md scale-105' : 'border-slate-200 hover:border-slate-300'"
        >
          <h3 class="font-bold text-slate-800">{{ item.label }}</h3>
          <p v-if="item.description" class="text-xs text-slate-500">{{ item.description }}</p>
        </div>
        
        <!-- Dot -->
        <div 
          class="absolute right-[-6px] w-3 h-3 rounded-full bg-slate-300 transition-colors duration-300 z-10"
          :class="isHighlighted(item.id) ? 'bg-blue-500' : ''"
        ></div>
      </div>
    </div>

    <!-- Connections Area -->
    <div class="w-[200px] relative" :style="{ height: `${Math.max(leftItems.length, rightItems.length) * 96}px` }">
      <svg class="w-full h-full overflow-visible">
        <path
          v-for="(conn, idx) in connections"
          :key="idx"
          :d="getConnectorPath(conn.from, conn.to)"
          fill="none"
          stroke-width="2"
          class="transition-all duration-300"
          :class="[
            (hoveredItem === conn.from || hoveredItem === conn.to) 
              ? 'stroke-blue-500 stroke-[3px]' 
              : 'stroke-slate-200'
          ]"
        />
      </svg>
    </div>

    <!-- Right Column -->
    <div class="flex flex-col gap-0 w-64">
      <div 
        v-for="item in rightItems" 
        :key="item.id"
        class="h-24 flex items-center justify-start pl-4 relative group cursor-pointer transition-all duration-300"
        @mouseenter="hoveredItem = item.id"
        @mouseleave="hoveredItem = null"
      >
        <!-- Dot -->
        <div 
          class="absolute left-[-6px] w-3 h-3 rounded-full bg-slate-300 transition-colors duration-300 z-10"
          :class="isHighlighted(item.id) ? 'bg-blue-500' : ''"
        ></div>

        <div 
          class="p-4 rounded-lg border-2 transition-all duration-300 w-full bg-white"
          :class="isHighlighted(item.id) ? 'border-blue-500 shadow-md scale-105' : 'border-slate-200 hover:border-slate-300'"
        >
          <h3 class="font-bold text-slate-800">{{ item.label }}</h3>
          <p v-if="item.description" class="text-xs text-slate-500">{{ item.description }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
